import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Censusblock } from '../services/censusblock';
import { CensusblockService } from '../services/censusblock.service';
import { BlockService } from '../services/block.service';
import { Block } from '../services/block';
import { User } from '../services/user.model';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-censusblock',
  templateUrl: './censusblock.component.html',
  styleUrls: ['./censusblock.component.css']
})
export class CensusblockComponent implements OnInit {
  @Output() pickedBlock = new EventEmitter<any>();
  censusblocks: Censusblock[];
  states:Array<string>;
  counties:Array<string>;
  tractblocks:Array<string>;
  blocks:Array<Block>;
  block: Block[];
  blocksbyStatus:Array<Block>;
  blocksbyUser:Array<Block>;
  blocksbyUserStatus:Array<Block>;
  blockUsers:Array<User>;
  //tractblock:string;
  alluserconstant: string = 'All Users';
  allstatusconstant: string = 'Any Status';

  blockstatuses: Array<string> = [this.allstatusconstant, 'Ready for QC', 'QC In Progress', 'QC Complete', 'In Progress', 'blank'];
  blockcriterion: Array<string> = ['Status', 'User'];

  searchBlock:string;
  showallblocks: boolean = false;
  //TODO remove all reference to this temp variable
  skipsection:boolean = true;

  constructor(
    private censusblockService: CensusblockService, 
    private blockService: BlockService, 
    private userService: UserService 
      ) { }

  ngOnInit() {
    this.getCensusblocks();
    this.getBlocks();
    this.getUsers();
  }

  getCensusblocks(): void {
    this.censusblockService.getCensusblocks()
    //////// the line below sort has not been tested    
    .subscribe(censusblocks => {
      this.censusblocks = censusblocks
      //objs.sort((a, b) => a.last_nom.localeCompare(b.last_nom));
      this.censusblocks.sort((a, b) => a.geoid10.toString().localeCompare(b.geoid10.toString()));
      this.states = Array.from(new Set(this.censusblocks.map(item =>item.state)));
    });    
  }
  
  getBlocks():void{
    this.blockService.getAllBlocks()
    .subscribe(blocks => {
      this.blocks = blocks;      
    });
  }

  getUsers():void{
    this.userService.getUsers()
    .subscribe(users => {
      let blank:User[] = [{'email':'','username': this.alluserconstant,'firstname': '','lastname': '','type': '','password': '','cpassword':''}];
      this.blockUsers = blank.concat(users);      
    });
  }
  selectedBlock(block){
    console.log ('start of SelectedBlock: ', block, Date());
    this.pickedBlock.emit(block);
    console.log ('end of SelectedBlock: ', Date());
  }

  updateCountyList(state){
    //filter by state
    let blank:string[] = [""];
    let tmp:Array<Censusblock> = this.censusblocks.filter(function(x){      
      return x.state == state;
    });
    //only return unique
    this.counties =  blank.concat(Array.from(new Set(tmp.map(item =>item.county))));
    //console.log ('length of list filtered by state ' + tmp.length + ' and length of counties is ' + this.counties.length);
  }

  updateTractList(county, state){
    //console.log('in updatecountylist county is ' + county + ' and state is ' + state);
    //filter by county and state
    let blank:string[] = [""];
    let tmp:Array<Censusblock> = this.censusblocks.filter((x) => x.county == county )
      .filter((x) => x.state == state)
      //filter out done if showallblocks is not checked
      console.log('new block status', this.blocks.find((s) => s.block == '530330042002002'));
      if(!this.showallblocks){
        let tmpdoneBlocksobj:Array<Block> = this.blocks.filter((x) => {
          return this.blockstatuses.indexOf(x.blockstatus) <4 && this.blockstatuses.indexOf(x.blockstatus)>0;
        });
        let tmpdoneBlocks = Array.from(new Set(tmpdoneBlocksobj.map(item =>item.block)));
        
        tmp = tmp.filter((x) => {
          return !(tmpdoneBlocks.indexOf(String(x.geoid10)) != -1);
        });
        //console.log("tmpdoneblocks", tmpdoneBlocks, 'tmp is',tmp);
      }
    //only return unique    
    this.tractblocks = blank.concat(Array.from(new Set(tmp.map(item =>item.tractblock))));
    //this.tractblock = null;
    //console.log ('length of list filtered by state ' + tmp.length + ' and length of counties is ' + this.tractblocks.length);
  }

  updateBlockStatusList(status){
    //filter by status
    console.log('in updateblocklist', status);
    let blank:Block[] = [{'block':""}];
    //this.getBlocks(); //it would be nice to only call this if new blocks have been added
    if(this.blocks){
      this.blocksbyStatus = blank.concat(this.blocks.filter((x) => {      
        if(this.blockstatuses.indexOf(status)==4){ //it's blank
          //console.log('in undefined:', x.blockstatus);
          return !x.blockstatus; 
        } else {        
          return x.blockstatus == status;
        }
      }));
    } else {return null}
    console.log('in updateblocklist length', this.blocksbyStatus.length);
  }
  
  updateBlockUserList(user){
    //filter by status
    console.log('in updateBlockUserList', user);
    let blank:Block[] = [{'block':""}];
    //this.getBlocks(); //it would be nice to only call this if new blocks have been added
    if(this.blocks){
      this.blocksbyUser = blank.concat(this.blocks.filter(function(x){
        return x.cataloger == user;
      }));
    } else {return null}
    console.log('in updateblocklist length', this.blocksbyUser.length);
  }

  updateBlockUserStatusList(status, user){
    //filter by status
    console.log('in updateBlockUserStatusList', status, user);
    let blank:Block[] = [{'block':""}];
    //this.getBlocks(); //it would be nice to only call this if new blocks have been added
    if(this.blocks){
      this.blocksbyUserStatus = blank.concat(this.blocks       
        .filter((x) => {
          if(status == this.allstatusconstant){
            return x;
          } else{
            return x.blockstatus == status;
        }})
        .filter((x) => {
          if(user == this.alluserconstant){
            return x;
          } else {
            return x.cataloger == user;
          }})
      );
      this.blocksbyUserStatus.sort((a, b) => a.block.toString().localeCompare(b.block.toString()));
    } else {return null}
    console.log('in updateBlockUserStatusList length', this.blocksbyUserStatus.length);
  }

  gotoTrackBlock(state, county, tractblock){
    let tmp:Array<Censusblock> = this.censusblocks
      .filter((x) => x.county == county )
      .filter((x) => x.state == state)
      .filter((x) => x.tractblock == tractblock)

    this.selectedBlock(tmp[0].geoid10);
  }

    // TODO: Remove this when we're done
    get diagnostic() { return JSON.stringify(this.tractblocks); }


}
