import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router/';
import { UserService } from '../services/user.service';
import { AuthService } from '../services/auth.service';
import { User } from '../services/user.model';

@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  user: User;
	error: string = '';
	success: string = '';

	constructor(private userService: UserService, private authService: AuthService, private router: Router) {
		this.user = new User('', '', '', '', '', '', '');
	}

	ngOnInit() {
	}

	login() {
		this.error = '';
		this.success = '';

		if (!this.user.email || !this.user.password) {
			this.error = 'Please input details';
			return;
		}
		
		this.userService.login(this.user).subscribe((data) => {
			if (data.error) {
				this.error = data.error;
			} else {
				
				let decode =this.parseJwt(data.token);
				console.log('return from login', decode);
				this.success = 'Log in successfully!';
				this.authService.setToken(data.token);
				this.userService.setCurrentUser(decode.username);
				this.router.navigate(['gmap']);
			}
		});
	}

	parseJwt(token){
		var base64Url = token.split('.')[1];
  		var base64 = base64Url.replace('-', '+').replace('_', '/');
  		return JSON.parse(window.atob(base64));

	}
}
