import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { MatSnackBar} from '@angular/material';

import { Site } from './site.model';
import { MessageService } from './message.service';
import { Globals } from '../globals'

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class SiteService {

  //use node server address
 private sitesUrl =  this.globals.awsapibase + 'sites';  // URL to web api

 constructor(
   private http: HttpClient,
   public snackBar: MatSnackBar,
   private messageService: MessageService,
   private globals:Globals
   )   { }

  /** GET Sites from the server */
  getAllSites (): Observable<Site[]> {
    return this.http.get<Site[]>(this.sitesUrl)
      .pipe(
        tap(sites => this.log('fetched ' + sites.length + ' sites')),
        catchError(this.handleError('getAllSites', []))
      );
  }

  getAllSitesByBlock (block): Observable<Site[]> {
    return this.http.get<Site[]>(this.sitesUrl + '/' + block + '/block')
      .pipe(
        tap(_ => this.log('fetched sites')),
        catchError(this.handleError('getAllSitesByBlock', []))
      );
  }
 
  getSitebyID (siteid): Observable<Site> {
    return this.http.get<Site>(this.sitesUrl + '/' + siteid )
      .pipe(
        tap(_ => this.log('fetched site by id ' + siteid)),
        catchError(this.handleError<any>('getSitebyID'))
      );
  }

    /** PUT: update the Site on the server */
    updateSite (site: Site): Observable<any> {
      console.log('in updatesite, putting',site);      
      return this.http.put(this.sitesUrl, site, httpOptions).pipe(
        tap(_ => {
          this.log(`updated site id=${site.siteID} on ${this.sitesUrl}`);
          //this.snackBar.open('Site updated',null, {duration:1500, horizontalPosition:'right', verticalPosition: 'top' });
          this.snackBar.open('Site updated',null, {duration:1500,  verticalPosition: 'top' });
        }),
        catchError(this.handleError<any>('updateSite'))
      );
    }

    //  Delete Site
    deleteSite(siteid):Observable<any>{
      return this.http.delete(this.sitesUrl + "/" + siteid, httpOptions).pipe(
        tap(_ => this.log(`deleted site id=${siteid}`)),
        catchError(this.handleError<any>('deleteSite'))
      );
    }

    //delete all sites on block
    deleteAllSitesbyBlock  (block): Observable<Site[]> {
      console.log('in siteservice deleteallblocks url', this.sitesUrl + "block/" + block);
      //return this.http.delete(this.sitesUrl + "block/" + block, httpOptions).pipe(
      return this.http.delete(this.sitesUrl + "block/" + block, httpOptions).pipe(
        tap(_ => console.log(`deleted sites for block=${block}`)),
        catchError(this.handleError<any>('deleteAllSitesbyBlock'))
      );
    }
       
  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
      alert('site service error. ' + error.message);
      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a Service message with the MessageService */
  private log(message: string) {
    this.messageService.add(`SiteService: ${message}`);
  }

}

