import { NgModule }       from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { FormsModule }    from '@angular/forms';
import { HttpClientModule }    from '@angular/common/http';

//import { HttpClientInMemoryWebApiModule } from 'angular-in-memory-web-api';
//import { InMemoryDataService }  from './in-memory-data.service';

import { AppRoutingModule }     from './app-routing.module';

import { AppComponent }         from './app.component';
import { MessagesComponent }    from './messages/messages.component';
import { CatalogComponent }     from './catalog/catalog.component';
import { CensusblockComponent } from './censusblock/censusblock.component';
import { GmapComponent }        from './gmap/gmap.component';
import { SiteComponent }        from './site/site.component';
import { BlockComponent }       from './block/block.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule } from './modules/material.module';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { SummaryComponent } from './summary/summary.component';
import { SitesumtableComponent } from './sitesumtable/sitesumtable.component';
import { AuthService } from './services/auth.service';
import { GroupbyPipe } from './groupby.pipe';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { SummapComponent } from './summap/summap.component';
import { Globals } from './globals';

//import {MatButtonModule, MatCheckboxModule} from '@angular/material';


@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MaterialModule,
    NgbModule.forRoot()

    // The HttpClientInMemoryWebApiModule module intercepts HTTP requests
    // and returns simulated server responses.
    // Remove it when a real server is ready to receive requests.
    /*
    HttpClientInMemoryWebApiModule.forRoot(
      InMemoryDataService, { dataEncapsulation: false }
    )
    */
  ],
  declarations: [
    AppComponent,
    MessagesComponent,    
    CatalogComponent,
    CensusblockComponent,
    GmapComponent,
    SiteComponent,
    BlockComponent,
    LoginComponent,
    SignupComponent,
    SummaryComponent,
    SitesumtableComponent,
    GroupbyPipe,
    SummapComponent
  ],
  providers: [AuthService, Globals],
  bootstrap: [ AppComponent ]
})
export class AppModule { }
