import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of, BehaviorSubject } from 'rxjs';
import { catchError, tap, publishBehavior } from 'rxjs/operators';

import { User } from './user.model';
import { MessageService } from './message.service';
import { Globals } from '../globals'

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class UserService {
  user:Observable<User>;
  private currentUserSubject = new BehaviorSubject<User>(undefined);
  private userURL = this.globals.awsapibase + 'user';  // URL to web api

  constructor(
   private http: HttpClient,
   private messageService: MessageService,
   private globals:Globals
   )   { }

   init(){
    this.refreshCurrentUser();
   }

  refreshCurrentUser(){
    this.user = this.getCurrentUser();
   }
   
  /** POST: user signup */
  signup (user: any): Observable<any> {
    return this.http.post(this.userURL + '/signup', user, httpOptions).pipe(
      tap(_ => {}),
      catchError(this.handleError<any>(''))
    );
  }

  /** POST: user login */
  login (user: any): Observable<any> {
    return this.http.post(this.userURL + '/login', user, httpOptions).pipe(
      tap(_ => {}),
      catchError(this.handleError<any>(''))
    );
  }

   /** GET users from the server */
  getUsers (): Observable<User[]> {
    return this.http.get<User[]>(this.userURL + 's')
      .pipe(
        tap(_ => this.log('fetched users')),
        catchError(this.handleError('getUsers', []))
      );
  }
/*** This whole thing might be better served by just reading the token so they don't ahve to relogin to fill this. */
  // Get current username
  getCurrentUser():Observable<User> {
    return this.currentUserSubject.asObservable();
  }

  setCurrentUser(current:User){
    this.currentUserSubject.next(current);
  }

  /** Log a Service message with the MessageService */
  private log(message: string) {
    this.messageService.add(`UserService: ${message}`);
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

}

