/// <reference types="@types/googlemaps" />
import { Component, OnInit,ViewChild } from '@angular/core';
import { BlockService } from '../services/block.service';
import { Block } from '../services/block';

declare const google;

@Component({
  selector: 'app-summap',
  templateUrl: './summap.component.html',
  styleUrls: ['./summap.component.css']
})

export class SummapComponent implements OnInit {
  @ViewChild('summap') mapElement: any;
  map: google.maps.Map;
  defLat:number = 46.073;
  defLng:number = -118.368;
  blocks:Array<Block>;
  blockLayer;

  blockstatuses: Array<string> = ['In Progress', 'Ready for QC', 'QC In Progress', 'QC Complete'];
  //colors: Array<string> = ['#000000', '#1E90FF', '#FF1493', '#32CD32', '#FFFF00', '#4B0082'];
  colors: Array<string> = ['black', 'cyan', 'greenyellow', 'fuchsia', 'orange'];
  symbols: Array<number> = [google.maps.SymbolPath.CIRCLE,google.maps.SymbolPath.CIRCLE, google.maps.SymbolPath.BACKWARD_OPEN_ARROW, 
    google.maps.SymbolPath.FORWARD_OPEN_ARROW, google.maps.SymbolPath.FORWARD_CLOSED_ARROW ];
  scales:Array<number> = [10,10,10,10,10];  
  constructor(
    private blockService:BlockService
  ) { }

  ngOnInit() {
    this.blockService.getAllBlocks()
    .subscribe(blocks => {
      this.blocks = blocks;
      //console.log ('in getblocks', this.blocks);
    
      //console.log ('after getblocks call', this.blocks);
      let mapProp = {
        center: new google.maps.LatLng(this.defLat, this.defLng),
        zoom: 19,
        //mapTypeId: google.maps.MapTypeId.ROADMAP
        mapTypeId: google.maps.MapTypeId.HYBRID,
        tilt: 0,
        scaleControl: true,
        zoomControlOptions: {
        position: google.maps.ControlPosition.LEFT_BOTTOM
        },
        fullscreenControlOptions: {
          position: google.maps.ControlPosition.BOTTOM_LEFT
        }
      };
      //var map = new google.maps.Map(document.getElementById('map')
      this.map = new google.maps.Map(this.mapElement.nativeElement, mapProp);
      this.blockLayer = new google.maps.FusionTablesLayer({
        query: {
          //Sample census blocks
          //https://www.google.com/fusiontables/DataSource?docid=181lhJJkYVFl3-SBjZ49gj_DPCNhJTGdhm_-Z0oAg
          select: '\'geometry\'',
          //where: 'name = ' + this.selectedBlock,
          from: '1UuMuU8demcoaoW2pIVc1P8nsD_YhpOzfa5RRsGwk'
        },
        options: {
          styleId: 2,
          templateId: 2
        }
      });
      this.blockLayer.setMap(this.map);
      var bounds = new google.maps.LatLngBounds();
      this.blocks.forEach(block => {      
        let coords = new google.maps.LatLng(block.cblat, block.cblon)
        bounds.extend(coords);
        //console.log('blockstatus', block.blockstatus);
        let color = this.colors[this.blockstatuses.indexOf(block.blockstatus)+1]; 
        //let path = this.symbols[this.blockstatuses.indexOf(block.blockstatus)+1]; 
        let path = this.symbols[0]; 
        let scale = this.scales[this.blockstatuses.indexOf(block.blockstatus)+1]; 
        new google.maps.Marker({
          position: coords,
          map: this.map,
          title: block.block,
          icon: {
            path: path,
            strokeColor: color,
            scale:scale
          } 
        });
      });
      this.map.fitBounds(bounds);
    });
  }

  getBlocks():void{
    this.blockService.getAllBlocks()
    .subscribe(blocks => {
      this.blocks = blocks;
      //console.log ('in getblocks', this.blocks);
    });
  }
}
