import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { Censusblock } from './censusblock';
import { MessageService } from './message.service';
import { Globals } from '../globals'

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};


@Injectable({
  providedIn: 'root'
})
export class CensusblockService {

  //  private censusblocksUrl = 'api/censusblocks';  // URL to web api  
  private censusblocksUrl = this.globals.awsapibase + 'censusblocks';  // URL to web api

  constructor(
    private http: HttpClient,
    private messageService: MessageService,
    private globals:Globals
    ) { }

  /** GET censusblocks from the server */
  getCensusblocks (): Observable<Censusblock[]> {
    return this.http.get<Censusblock[]>(this.censusblocksUrl)
      .pipe(
        tap(censusblocks => this.log('fetched censusblocks')),
        catchError(this.handleError('getCensusblocks', []))
      );
  }

  getCensusblockbyID (cbid): Observable<Censusblock[]> {
    let callURL = this.censusblocksUrl + '/' + cbid;
    console.log('getcbid ' + cbid + 'and the url: ' + callURL);
    if (cbid=""){
      console.log('no passed cbid, returning null');
      return null;
    }
    return this.http.get<Censusblock[]>(callURL )
      .pipe(
        tap(censusblocks => this.log('fetched censusblock by id ' + censusblocks)),
        catchError(this.handleError('getCensusblocks', []))
      );
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a Service message with the MessageService */
  private log(message: string) {
    this.messageService.add(`CensusblockService: ${message}`);
  }

}
