//This is where the Client side routing is defined
import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { SummaryComponent }  from './summary/summary.component';
import { LoginComponent }  from './login/login.component';
import { SignupComponent }  from './signup/signup.component';
import { AuthService } from './services/auth.service';
import { GmapComponent }  from './gmap/gmap.component';

const routes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'signup', component: SignupComponent, canActivate: [AuthService] },
  { path: 'summary', component: SummaryComponent, canActivate: [AuthService] },  
  //{ path: 'gmap', component: GmapComponent, canActivate: [AuthService] } 
  { path: 'gmap', component: GmapComponent } 
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
