import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { MessageService } from './services/message.service';

import { FusionData } from './fusiontabledata';


@Injectable({
  providedIn: 'root'
})
export class FusiontabledataService {
  private urlBase = 'https://www.googleapis.com/fusiontables/v2/query?sql=SELECT+geometry+FROM+1UuMuU8demcoaoW2pIVc1P8nsD_YhpOzfa5RRsGwk+WHERE+name=';  // URL to web api
  private fusionUrl: string; 
  //geometry:any;
  constructor(
    private http: HttpClient,
    private messageService: MessageService) { }

  /** GET fusiontable data from the server */

  getFusionTableData (geoid): Observable<FusionData> {    
    this.fusionUrl = this.urlBase + geoid + '&key=AIzaSyBdxTJFQjbFwatyJX71blXQK7JVPXLdQVQ'; 
    //console.log('in get fusion table data id is ' , geoid , ' and url is ' , this.fusionUrl);
    return this.http.get<FusionData>(this.fusionUrl)
    .pipe(
        tap(fusiontable => this.log('fetched fusion table data')),
        catchError(this.handleError('getFusionTableData',new FusionData()))
      );      
  }  
  
/**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a Service message with the MessageService */
  private log(message: string) {
    this.messageService.add(`FusiontabledataService: ${message}`);
  }  
}
