import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { tap } from 'rxjs/operators';

import { Block } from './block';
import { BlockService } from './block.service';
import { Site } from '../services/site.model';
import { SiteService } from './site.service';

@Injectable({
  providedIn: 'root'
})
export class CurrentBlockService {
  blocks$: Observable<Block[]>;
  blockSites$: Observable<Site[]>;
  //sites: Site[];

  private selectedBlockSubject = new BehaviorSubject<Block>(undefined);
  currentBlock = this.selectedBlockSubject.asObservable();
  
  private blockListSubject = new BehaviorSubject<Site[]>(undefined);
  blockSites = this.blockListSubject.asObservable();

  constructor(
    private blockService: BlockService, 
    private siteService:SiteService
    ) {
    this.init();
   }

  init(): void{
    this.refreshBlocks();
  }

  refreshBlocks(): void {
    this.blocks$ = this.blockService.getAllBlocks();
  }

  /*
  refreshBlockSites():void{
    //there is an odd type problem with Site[]
    this.blockSites$ = this.siteService.getAllSites();
  }
*/

  getSelectedBlock(): Observable<Block> {
    return this.selectedBlockSubject.asObservable();
  }

  setSelectedBlock(selected: Block): void {
    this.selectedBlockSubject.next(selected);
  }

  getBlockSites(): Observable<Site[]>{
    return this.blockListSubject.asObservable();
  }

  setBlockSites(selected:Block): void{
    let siteslocal:Site[];
    //let blockList:Site[];
    this.siteService.getAllSites().subscribe(sites => {            
      siteslocal = sites;      
      if(!(typeof selected ==='undefined')){
        this.blockListSubject.next(siteslocal.filter(function(x){
          return x.block == selected.block;
        }));
      } else{
        this.blockListSubject.next(siteslocal)
      }    
      console.log('in setBlockSites ' + selected.block + ' yields ' + this.blockListSubject.value.length);
    });                   
  }  

  updateBlock(updated:Block): Observable<Block>{
    return this.blockService.updateBlock(updated)
      .pipe(
      /* If the update is submitted successfully, refresh
        the relevant data sources in this service */
        tap(block => {
          this.setSelectedBlock(block);
          this.refreshBlocks();
        })
      );    
  }
}

